
body{
		background: #fff;
}
.ellipsis{
		overflow:hidden;
		white-space:nowrap;
		text-overflow:ellipsis;
}
.ellipsis-list-2{
		    display: -webkit-box;
    -webkit-line-clamp: 2;
    overflow: hidden;
}
:after, :before{
	    border: 0!important;
}
.yd-list-theme4{
		padding:0 2%;
}
.yd-list-title{
		font-size:.3rem;
		color:#3e3e3e;
		text-align: left;
		font-weight: 600;
}
.yd-list-theme4 .yd-list-item .yd-list-img{
	height:auto;
	padding-bottom: 0;
}		



